import React from 'react';
import './NavigationBar.css'
import {NavLink} from "react-router-dom";
import {routes} from "../../../Routes/Routes";

function NavigationBar(props) {

    return (
        <nav className={'navigationBar'}>
            <ul>
                <li><NavLink exact activeClassName={'active'} to={routes.home.path}>Home</NavLink></li>
                <li><NavLink activeClassName={'active'} to={routes.nouns.path}>Nouns</NavLink></li>
                <li><NavLink activeClassName={'active'} to={routes.verbs.path}>Verbs</NavLink></li>
            </ul>
        </nav>
    )
}


export default NavigationBar;