import React, {useState} from 'react';
import './ListOfVerbs.css'
import data from '../../../Data/Verbs.json'

function ListOfVerbs(props) {

    // function shuffle(a) {
    //     for (let i = a.length - 1; i > 0; i--) {
    //         const j = Math.floor(Math.random() * (i + 1));
    //         [a[i], a[j]] = [a[j], a[i]];
    //     }
    //     return a;
    // }
    //
    // console.log(JSON.stringify(shuffle(data)));

    return (
        <div className={'listOfVerbs'}>
            <ul>
                {data.map((element, index) => index < 10 ? <Verb {...element} key={element.icelandic}/> : null)}
            </ul>
        </div>
    );
}

function Verb(props) {

    const [englishTranslation, setEnglishTranslation] = useState(false);
    const handleClick = () => {
        setEnglishTranslation(!englishTranslation);
    };

    return (
        <li onClick={handleClick}>
            <span>
                {props.icelandic}
            </span>
            <span>
                {englishTranslation ?
                    props.english[0] :
                    props.russian[0]}
            </span>
        </li>
    )
}

export default ListOfVerbs;